import { FC, ReactNode } from 'react'

/**
 * Simplified LabelValuePairVertical component for Ziggy plugins
 */

export interface LabelValuePairVerticalProps {
  label: string
  className?: string
  children?: ReactNode | ReactNode[]
}

const LabelValuePairVertical: FC<LabelValuePairVerticalProps> = ({
  label,
  className = '',
  children,
}) => (
  <div className="flex flex-col w-full">
    <div className={`w-full mb-[2px] ${className}`}>{label}</div>
    {children}
  </div>
)

export default LabelValuePairVertical
